import { cloneObj, getRandomValue, getUID } from "./otherFunctions"
import UnitStates from "../game resources/unitStates"
import * as CHECK_EFFECT from "../functions/checkEffect"


export function setUnitStats(unit, statsArr, valuesArr, bmap, setBmap, setUnits) {
    let newUnit = cloneObj(unit), updatedValues = {}
    statsArr.forEach((stat, index) => {
        newUnit[stat] = valuesArr[index]
        updatedValues[stat] = valuesArr[index]
    })

    //если изменились координаты - изменить карту
    if (unit.x !== newUnit.x || unit.y !== newUnit.y) {
        let newBmap = bmap.map(arr => arr.slice())
        newBmap[unit.y][unit.x].unitID = null
        newBmap[newUnit.y][newUnit.x].unitID = newUnit.unitID
        setBmap(newBmap)
    }

    /*
    //предыдущий вариант
    setUnits(prevState => prevState.map(item => {
        return (item.unitID === unit.unitID) ? {...item, ...updatedValues} : item
    }))*/

    setUnits(prevState => prevState.map(item => {
        if (item !== null && item.unitID === unit.unitID) {
            statsArr.forEach((stat, index) => item[stat] = parseInt(valuesArr[index]))
            return { ...item }
        } 
        else return item
    }))
}

export function modifyUnitStats(unit, statsArr, valuesArr, setUnits, setQueue) {
    setUnits(prevState => prevState.map(item => {
        if (item !== null && item.unitID === unit.unitID) {
            statsArr.forEach((stat, index) => {
                let value = parseInt(valuesArr[index])
                if (stat === 'AC') item['physRes'] += value * 4
                item[stat] = item[stat] + value 
                //если появился излишек по стату
                if ((stat === 'HP' || stat === 'Mana' || stat === 'WP') && value > 0) {
                    if (item[stat] > item['max' + stat]) item[stat] = item['max' + stat]
                }
            })
            //проверка на смерть
            if (item.alive && item['HP'] === 0) killUnit(item.unitID, setQueue, setUnits)
            return { ...item }
        } 
        else return item
    }))
}

//убить с-во, но оставить его в списке существ мертвым (чтобы можно было считать трупы + воскрешать)
export function killUnit(unitID, setQueue, setUnits) {
    setUnits(prevState => prevState.map(unit => {
        if (unit !== null && unit.unitID === unitID) return {...unit, alive: false}
        else return unit
    }))
    setQueue(prevState => prevState.filter(item => item !== unitID))
}

//изменить поверхность
export function modifySurface(targetPos, setBmap, incompatibleTerrains, surfacesTable) {
    let i = targetPos.i, j = targetPos.j 
        setBmap(prevState => prevState.map((row, rowNum) => {
            return row.map((cell, colNum) => {
                if (i === rowNum && j === colNum) {
                    if (!incompatibleTerrains.includes(cell.subtype)) 
                        //возвращает поверхность в зависимости от того, какая в данной клетке поверхность сейчас
                        return {...cell, surface: surfacesTable[cell.surface]}
                    else return cell
                } else return cell
            })
        }))
}


//ПРОВЕРКА СТАТУСА


export function checkStatus(status, unit) {
    if (unit.statuses.some(item => item === status)) 
        return true
    else return false
}

//РАСЧЕТ УРОНА / ХИЛА

//получить результирующее значение урона / хила ДО влияния условий / резистов
export function getPrimaryValue(dmg, dmgType, caster) {
    let {str, int, will, intMul, strMul, agiMul} = caster
    //multiplier - на что домножить базовый урон, чтобы получился итоговый
    //statBonus - бонус от множителя стата
    //statMultiplier - бонус от самого стата
    let actDmg = [0, 0], multiplier = 1, statExtraBonus = 0, statMultiplier = 0.02
    //для урона
    if (dmgType[1] === 'dmg') {
        if (dmgType[0] === 'phys') multiplier = 1 + str * statMultiplier
        else if (dmgType[0] === 'pure' || dmgType[0] === 'poison' || dmgType[0] === 'mana') multiplier = 1
        else if (dmgType[0] === 'will') multiplier = 1 + will * statMultiplier
        else multiplier = 1 + int * statMultiplier//для элементального / темного / светлого  урона
    }
    //для хила
    else if (dmgType[1] === 'heal') {
        if (dmgType[2] === null) multiplier = 1
        else if (dmgType[2] === 'int') multiplier = 1 + int * statMultiplier
        else if (dmgType[2] === 'will') multiplier = 1 + will * statMultiplier
        else multiplier = 1
    }
    //если есть бонус от множителя стата
    if (dmg[3] === 'intMul') statExtraBonus = dmg[2] * intMul
    actDmg[0] = Math.round(dmg[0] * multiplier) + statExtraBonus
    actDmg[1] = (dmg[1] !== null) ? Math.round(dmg[1] * multiplier) + statExtraBonus : null
    return actDmg
}

//получить результирующее значение урона ПОСЛЕ влияния условий / резистов
export function getActualDmg(dmgSource, caster, targetUnit, stat, dmgRecalulates = true) {
    let statMul = null, statMulValue = 0, dmgBonus = 0, {dmg, dmgType} = dmgSource
    //действует ли заклинание на данное с-во
    if (dmgSource.name === 'Death ripple' && caster.unitID === targetUnit.unitID) return [0, 0]
    //проверка на блокирование урона (для ХП)
    if (!CHECK_EFFECT.canBeDamaged(targetUnit)) return [0, 0]
    if (stat === 'HP' && checkCondition('Aegis', targetUnit)) return [0, 0]
    if (stat === 'WP' && CHECK_EFFECT.hasMindProtection(targetUnit)) return [0, 0]

    //если есть влияние модификаторов характеристик
    if (dmg.length > 2 && caster !== null) {
        statMulValue = dmg[2]
        statMul = dmg[3]
        dmgBonus = statMulValue * caster[statMul]
    } 
    //фактический урон (если не пересчитывается, то остается как есть)
    let actDmg = (dmgRecalulates) ? getPrimaryValue(dmg, dmgType, caster) : dmg
    let minDmg = actDmg[0] + dmgBonus
    let maxDmg = (actDmg[1] !== null) ? actDmg[1] + dmgBonus : null

    if (stat === 'HP') {
        //если есть мультипликативные бонусы к урону
        if (caster !== null && dmgRecalulates) {
            let multiBonus = 1
            if (checkCondition('Blood lust', caster) && checkState('Bleeding', targetUnit)) multiBonus += 0.15
            minDmg = minDmg * multiBonus
            maxDmg = (maxDmg !== null) ? maxDmg * multiBonus : null
        }
        
        //расчет результируюшего урона в зависимости от типа урона
        let resMultiplier
        if (dmgType[0] === 'poison' || dmgType[0] === 'pure') resMultiplier = 1
        else resMultiplier = 1 - (targetUnit[dmgType[0] + 'Res'] / 100)
        if (resMultiplier < 0) resMultiplier = 0
        minDmg = Math.floor(minDmg * resMultiplier)
        maxDmg = (maxDmg !== null) ? Math.floor(maxDmg * resMultiplier) : null
    }
    else if (stat === 'WP') {
        let willResMultiplier = 0.5//сколько резиста к Воле добавляется за 1 очко
        let targetWillRes = Math.floor(targetUnit['will'] * willResMultiplier)
        let resMultiplier = 1 - (targetWillRes / 100)
        if (resMultiplier < 0) resMultiplier = 0
        minDmg = Math.floor(minDmg * resMultiplier)
        maxDmg = (maxDmg !== null) ? Math.floor(maxDmg * resMultiplier) : null
    }
    //проверка на блесс / курсу
    if (caster !== null) {
        if (dmgRecalulates && checkCondition("Bless", caster)) minDmg = maxDmg
        else if (dmgRecalulates && checkCondition("Curse", caster)) maxDmg = minDmg
    }
    return [minDmg, maxDmg]
}

//вернуть значение результирующего урона
function getResultDmgValue(dmgSource, caster, targetUnit, stat, setUnits, dmgRecalulates = true) {
    //проверка на блокирование урона (для ХП)
    if (stat === 'HP' && checkCondition('Aegis', targetUnit)) {
        checkAndRemoveCondition('Aegis', targetUnit, setUnits)
        return 0
    }
    let actDmg = getActualDmg(dmgSource, caster, targetUnit, stat, dmgRecalulates)
    return (actDmg[1] !== null) ? getRandomValue(actDmg[0], actDmg[1]) : actDmg[0]
}

//получить результирующее значение хила ПОСЛЕ влияния условий
export function getActualHeal(healSource, caster, targetUnit, stat, dmgRecalulates = true) {
    if (!CHECK_EFFECT.isHealable(targetUnit)) return [0, 0]
    let healMultiplier = 1.0, {dmg, dmgType} = healSource

    //фактический хил (если не пересчитывается - остается как есть)
    let actHeal = (dmgRecalulates) ? getPrimaryValue(dmg, dmgType, caster) : dmg
    let minHeal = actHeal[0]
    let maxHeal = (actHeal[1] !== null) ? actHeal[1] : null

    if (stat === 'HP') {
        //проверка на болезнь и яд
        if (checkState('Diseased', targetUnit)) healMultiplier *= 0.5
        if (checkState('Greater poison', targetUnit)) healMultiplier *= 0.5
        minHeal = minHeal * healMultiplier
        maxHeal = (maxHeal !== null) ? maxHeal * healMultiplier : null

        //проверка на блесс / курсу
        if (caster !== null) {
            if (dmgRecalulates && checkCondition("Bless", caster)) minHeal = maxHeal
            else if (dmgRecalulates && checkCondition("Curse", caster)) maxHeal = minHeal
        }
    }
    
    return [minHeal, maxHeal]
}

//вернуть значение результирующего хила
function getResultHealValue(healSource, caster, targetUnit, stat, dmgRecalulates = true) {
    let actHeal = getActualHeal(healSource, caster, targetUnit, stat, dmgRecalulates)
    return (actHeal[1] !== null) ? getRandomValue(actHeal[0], actHeal[1]) : actHeal[0]
}

//итоговое значение урона / хила с учетом минимального / максимльного запаса характеристики
//dmgRecalulates: false - урон не пересчитывается, а остается таким же, как на входе, true - пересчитывается
export function getFinalValue(dmgSource, caster, targetUnit, stat, maxStat, setUnits, dmgRecalulates = true) {
    let {dmg, dmgType} = dmgSource
    if (dmg[0] === 0 && (dmg[1] === null || dmg[1] === 0)) return 0//если урон равен 0
    if (dmgType[1] === 'dmg') {
        let actDmg = getResultDmgValue(dmgSource, caster, targetUnit, stat, setUnits, dmgRecalulates)
        //может ли хп опуститься ниже 1
        if (stat === 'HP' && !CHECK_EFFECT.canDie(targetUnit)) {
            return (targetUnit['HP'] - actDmg >= 1) ? actDmg : targetUnit[stat] - 1
        }
        else return (targetUnit[stat] - actDmg >= 0) ? actDmg : targetUnit[stat]
    }
    else if (dmgType[1] === 'heal') {
        if (targetUnit[stat] < targetUnit[maxStat]) {
            let actHeal = getResultHealValue(dmgSource, caster, targetUnit, stat, dmgRecalulates)
            return (targetUnit[stat] + actHeal <= targetUnit[maxStat]) ? actHeal : targetUnit[maxStat] - targetUnit[stat]
        }
        else return 0
    }
}

//ЭФФЕКТЫ ЗАКЛИНАНИЙ

//снизить действие эффекта на 1 ход
export function decreaseConditionDuration(condition, unit, setUnits) {
    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            conditions: item.conditions.map(cond => {
                if (condition.spell.name === cond.spell.name && condition.UID === cond.UID) {
                    return {...cond, duration: cond.duration - 1}
                } else return cond
            })} : item
    }))
}

//добавить эффект
export function addNewCondition(spell, source, duration, unit, effect, setUnits) {
    let newCondition = {
        spell: cloneObj(spell),
        source: cloneObj(source),
        duration,
        effect,
        cumulative: (effect.cumulative === true) ? true : false,//накапливаемый эффект
        dispellable: (effect.dispellable === false) ? false : true,//возможность снять эффект
        delayedEffect: (effect.delayedEffect) ? effect.delayedEffect : null,//отложенный эффект (срабатывает на начало хода с-ва)
        positiveness: effect.positiveness,
        UID: getUID()
    }

    //если такой эффект уже имеется и он не накапливается
    if (!newCondition.cumulative && checkCondition(newCondition.spell.name, unit)) {
        setUnits(prevState => prevState.map(item => {
            return (item !== null && item.unitID === unit.unitID) ? {...item, conditions: item.conditions.map(cond => {
                //если это тот же самый эффект и его длительность больше либо равна (иначе не накладывается)
                //если длительности равны, значит второе точно продержится дольше, т.к. накладывалоь после
                if (cond.spell.name === newCondition.spell.name && cond.duration <= newCondition.duration) {
                    return {...cond, source: newCondition.source, duration: newCondition.duration}
                } else return cond
            })} : item
        }))
    }
    else {
        if (newCondition.effect.statsArr.length) {
            modifyUnitStats(unit, 
                effect.statsArr, 
                effect.valuesArr,
                setUnits)
        }    

        setUnits(prevState => prevState.map(item => {
            return (item !== null && item.unitID === unit.unitID) ? {...item, conditions: [...item.conditions, {...newCondition}]} : item
        }))
    }

    //если эффект накладывает состояния
    if (newCondition.effect.state) addNewState(newCondition.effect.state, duration, source, unit, setUnits)
}

//убрать эффект
export function removeCondition(condition, unit, setUnits, flag = "REMOVE_DEFAULT") {
    //отменяем действие эффектов (вычитаем прибавленные бонусы)
    if (condition.effect.statsArr.length) {
        let statsArr = condition.effect.statsArr
        let negativeValuesArr = condition.effect.valuesArr.map((item, index) => {
            //для ОП и ОД модифицирование статов происходит немного иначе (ВОЗМОЖНО НЕ НАДО)
            /*if (statsArr[index] === "MP" || statsArr[index] === "AP") {
                //чтобы при наложении не "дублировался" эффект
                if (flag === "REMOVE_DEFAULT") return 0
                //чтобы бонусное оп после окончания эффекта спадало
                else if (flag === "REMOVE_BY_DURATION") return -parseInt(item)
            }
            else */return -parseInt(item)
        })
        modifyUnitStats(unit, 
            condition.effect.statsArr, 
            negativeValuesArr,
            setUnits)
    }

    //если эффект накладывает состояние - состояние также снимается (МБ И НЕ НАДО!!!!!!!!!!!!!)
    if (condition.effect.state) removeState(condition.effect.state, unit, setUnits)
    
    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            conditions: item.conditions.filter(cond => !(cond.spell.name === condition.spell.name && cond.UID === condition.UID))} : item
    }))
}

//проверка на наличие эффекта
export function checkCondition(conditionName, unit) {
    if (unit.conditions.some(item => item.spell.name === conditionName)) 
        return true
    else return false
}

//проверить и, если есть, убрать
export function checkAndRemoveCondition(conditionName, unit, setUnits) {
    unit.conditions.forEach(item => {
        if (item.spell.name === conditionName) {
            removeCondition(item, unit, setUnits)
            return
        }
    })
}

//СОСТОЯНИЯ

//добавить состояние
export function addNewState(state, duration, source, unit, setUnits) {
    let newState = {
        name: state.name,
        runame: state.runame,
        img: state.img,
        description: state.description,
        type: state.type,
        dispellable: state.dispellable,
        duration,
        positiveness: state.positiveness,
        source,
        effect: state.effect,
        delayedEffect: (state.delayedEffect) ? state.delayedEffect : null,//отложенный эффект (срабатывает на начало хода с-ва)
        UID: getUID()
    }

    //вспомогательная ф-ия для проверки является ли стат основным
    function isPriorStat(stat) {
        if (stat === 'vit' || stat === 'wis' || stat === 'str' || 
            stat === 'agi' || stat === 'int' || stat === 'will') return true
        else return false
    }

    //если такой эффект уже имеется - накладывается состояние с максимальной длительностью
    //(ДЛЯ ЯДОВ НЕ РАБОТАЕТ, ОНИ СТАКАЮТСЯ)
    if (newState.name !== "Lesser poison" && newState.name !== "Greater poison" && checkState(newState.name, unit)) {
        setUnits(prevState => prevState.map(item => {
            return (item !== null && item.unitID === unit.unitID) ? {...item, states: item.states.map(st => {
                //если это то же самое состояние и его длительность больше либо равна (иначе не накладывается)
                //если длительности равны, значит второе точно продержится дольше, т.к. накладывалоь после
                if (st.name === newState.name && st.duration <= newState.duration) {
                    return {...st, source: newState.source, duration: newState.duration}
                } else return st
            })} : item
        }))
    } else {
        let resultValuesArr = [...newState.effect.valuesArr]
        if (newState.effect.statsArr.length) {
            let statsArr = newState.effect.statsArr, valuesArr = newState.effect.valuesArr
            statsArr.forEach((stat, index) => {
                //если это основной стат и изменяемое значение вычисляется в процентах - вычисляем фактическое значение
                if (isPriorStat(stat) && valuesArr[index].slice(-1) === '%') {
                    resultValuesArr[index] = Math.ceil(parseInt(valuesArr[index]) / 100 * unit[stat])
                }
            })
            modifyUnitStats(unit, 
                newState.effect.statsArr, 
                resultValuesArr,
                setUnits)
        }
    
        setUnits(prevState => prevState.map(item => {
            //первоначальный вариант
            //return (item.unitID === unit.unitID) ? {...item, states: [...item.states, {...newState}]} : item
            //valuesArr может поменяться, если на вход пришли %-нты, а в результате нужны конечные значения
            return (item !== null && item.unitID === unit.unitID) ? 
            {...item, states: [...item.states, {...newState, effect: {...newState.effect, valuesArr: resultValuesArr}}]}
            : item
        }))
    }
}

//проверка на наличие состояния
export function checkState(stateName, unit) {
    if (unit.states.some(item => item.name === stateName)) 
        return true
    else return false
}

//проверить и, если есть, убрать
export function checkAndRemoveState(stateName, unit, setUnits) { 
    unit.states.forEach(st => {
        if (st.name === stateName) {
            removeState(st, unit, setUnits)
            return
        }
    })
}

//снизить действие статуса на 1 ход
export function decreaseStateDuration(state, unit, setUnits) {
    //перманентные состояния не снимаются
    if (state.duration === "permanent") return

    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            states: item.states.map(st => {
                if (state.name === st.name && state.UID === st.UID) {
                    return {...st, duration: st.duration - 1}
                } else return st
            })} : item
    }))
}

//убрать состояние
export function removeState(state, unit, setUnits) {
    //перманентные состояния не снимаются
    if (state.duration === "permanent") return

    //отменяем действие эффектов (вычитаем прибавленные бонусы)
    if (state.effect.statsArr.length) {
        let negativeValuesArr = state.effect.valuesArr.map(item => -parseInt(item))
        modifyUnitStats(unit, 
            state.effect.statsArr, 
            negativeValuesArr,
            setUnits)
    }
    
    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            states: item.states.filter(st => !(state.name === st.name && state.UID === st.UID))} : item
    }))
}


//ПРОЧЕЕ


export function summonCreature(creature, caster, targetPos, setUnits, setBmap, setQueue) {
    let x = targetPos.j, y = targetPos.i 
    setUnits(prevState => [...prevState, creature])
    setBmap(prevState => prevState.map((row, i) => {
        return row.map((cell, j) => {
            if (i === y && j === x) {
                return {...cell, unitID: creature.unitID}
            } else return cell
        })
    }))
    
    setQueue(prevState => {
        let casterId = prevState.indexOf(caster.unitID)
        return [...prevState.slice(0, casterId + 1), creature.unitID, ...prevState.slice(casterId + 1)]
    })
}